.back-to-last {
  padding: 2vw;
  border-bottom: 1px #ececec solid;
  font-size: 1.2em;

  a {
    color: gray;
  }
}

.work-list-cell-index {
  margin: 2vw 0;
  padding: 1vw 4vw;
  background-color: white;
  width: 100%;

  .work-cell-row {
    margin: 2vw;
    display: flex;
    //flex-wrap: nowrap;
    justify-content: space-between;

    .work-name {
      font-size: 1.3em;
      font-weight: 500;
    }

    .row-1-right {
        flex-basis: 40%;
      .work_money {
        color: #fa8163;
        font-size: 1.5em;
        font-weight: 600;
      }
      .work-person-number{

      }
      .need-person{
        font-size: 10px;
      }
    }

    .work_tpl {
      padding: 1vw;
      border-radius: 5px;
      background-color: #f0f0f0;
      color: #939393;
    }

    .button-more {
      flex-shrink: 0;
    }
  }
}

.cell-disable {
  background-color: #e1e1e1;
}
