.Nav {
    position: fixed;
    bottom: 0;
    right: 0;
    left: 0;
    z-index: 1;
    color: rgb(102, 102, 102);
    font-size: 10px;
    background: #fff;
    border-top: 0.5px solid rgb(223, 223, 223);
    ul {
        display: flex;
        margin: 0;
        padding: 1vh;
        list-style: none;
        justify-content: center;
        li {
            flex-grow: 1;
            position: relative;
            a {
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                font-size: 15px;
                height: 5vh;
                text-decoration: none;
                background: transparent;
            }
        }
        .active {
            // color:blue;
            font-size: 17px;

        }
        .unactive{
            color: gray;
        }
    }
}
