*{
    border: none;

}

.vseller {
    background: url(https://www.almondlee.cn/public/photo) no-repeat;
    width: 100%;
    height: 100vh;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    //background-color: lightgray;
    text-align: center;
    .seller-container {
        width: 90%;
        .seller-title {
            width: 100%;
            font-size: 25px;
            font-weight: bold;
            letter-spacing: 20px;
            padding-left: 20px;
            box-sizing: border-box;
            margin-bottom: 10px;
            color: #000;
        }
        .seller-text {
            width: 100%;
            font-size: 14px;
            padding: 0 0 10px 6px;
            letter-spacing: 6px;
            box-sizing: border-box;
            margin-bottom: 50px;
            color: #666;
            border-bottom: 1px solid #fff;
        }
        .seller-search {
            display: flex;
            justify-content: center;
            align-items: center;

            .seller-getid {
                height: 30px;
                width: 200px;
                padding: 10px 35px;
                box-sizing: border-box;
                letter-spacing: 3px;
                text-align: center;
                outline: none;
                border-radius: 50px;
                //background-color: lightgray;
                color: #888;
                font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
            }
            button::after {
                border: none;
            }
            button {
                margin: 0;
                padding: 0;
                background-color: rgba(0, 0, 0, 0);
            }
            .seller-search-btn {
                width: 30px;
                height: 30px;
                position: absolute;
                margin-left: 80px;
            }
            .seller-search-btn img {
                width: 30px;
            }
            ::-webkit-input-placeholder {
                /* WebKit browsers */
                color: #aaa;
            }
        }
        [class*="cloud"] {
            position: fixed;
            bottom: 100px;
            background-color: #fff;
            border-radius: 50%;
        }
        .cloud1 {
            width: 150px;
            height: 150px;
            left: -30px;
            top: 70vh;
        }
        .cloud2 {
            width: 250px;
            height: 300px;
            left: 33px;
            top: 65vh;
        }
        .cloud3 {
            width: 200px;
            height: 200px;
            left: 200px;
            top: 70vh;
        }
    }
}
.frap {
    position: fixed;
    bottom: 9vh;
    right: 1vw;
    z-index: 996;

    button {
        height: 7vh;
        //padding: 4vw;
        font-size: 1.3em;
        background-color: #71ceff;
        color: #FFF;
        border: 1px solid #71ceff;
        border-radius: 48px;
        font-weight: 700;
        box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.38);
        animation: pop 0.1s ease-out;
        transition-property: background, border-color, color, transform;
    }
}