.forms{
    z-index: 100000000;
}
.view-container {
    width: 100%;
    height: 93vh;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    background-color: #c3ddeb;
    text-align: center;
    .seller-container {
        width: 90%;
        .seller-title {
            width: 100%;
            font-size: 25px;
            font-weight: bold;
            letter-spacing: 20px;
            padding-left: 20px;
            box-sizing: border-box;
            margin-bottom: 10px;
            color: #000;
        }
        .seller-text {
            width: 100%;
            font-size: 14px;
            padding: 0 0 10px 6px;
            letter-spacing: 6px;
            box-sizing: border-box;
            margin-bottom: 20px;
            color: #666;
            border-bottom: 1px solid #fff;
        }
        .btns {
            margin: 10vw;
        }
    }
    .forms{
        padding: 10vw;
        z-index: 100;
        background-color: white;
        border-radius: 5px;
    }
    [class*='cloud'] {
        position: fixed;
        bottom: 100px;
        background-color: #fff;
        border-radius: 50%;
    }
    .cloud1 {
        width: 150px;
        height: 150px;
        left: -30px;
        top: 70vh;
    }
    .cloud2 {
        width: 250px;
        height: 300px;
        left: 33px;
        top: 65vh;
    }
    .cloud3 {
        width: 200px;
        height: 200px;
        left: 200px;
        top: 70vh;
    }
}
