.vproductShow-card-container {
    margin: 0;
    padding: 0;
    list-style: none;
    .vproductShow-card-cell {
        // border-bottom:1px solid #e9e9e9;
        border-radius: 10px;
        margin: 5vw;
        padding: 5vw 5vw;
        background-color: white;
        box-shadow: 0 5px 10px 0 rgba(174, 174, 174, 0.4);
        display: flex;
        align-items: center;
        img {
            width: 100%;
        }
        h2 {
            margin: 0;
        }
        .goods-bottom{
            display: flex;
            justify-content: center;
            align-items: center;
            .icon-favorite{
                display: inline-block;
                padding: 5%;
            }
            .description-goods{
                display: inline-block;
                flex-grow: 1;
                width: 100%;
                text-align: center;
            }
            .icon-share{
                display: inline-block;
                padding: 5%;

            }
        }
        .infs {
            margin: 5px;
            color: gray;
        }
        .price-and-pop {
            margin: 1px;
            .price {
                font-size: 20px;
                font-weight: bold;
                // display: inline-block;
                padding: 3px 10px;
                border-radius: 50px;
                color: rgba(201, 160, 47, 0.9);
                background-color: rgba(255, 231, 166, 0.9);
            }
            .pop {
                margin-left: 10px;
                // display: inline-block;
                width: 40px;
                height: 18px;
                font-size: 14px;
                line-height: 18px;
                letter-spacing: 1px;
                // text-align: center;
                border-radius: 15px;
                color: #999;
                border: 1px solid #999;
            }
        }
        .button-copy {
            height: 30px;
            background-image: linear-gradient(
                to right,
                #ff9000 0,
                #ff5000 100%
            );
            border: none;
            border-radius: 60px;
            font-size: 18px;
            color: white;
            margin: 2px;
            padding: 0px 10px;
        }
        .button-share {
            height: 30px;
            background-image: linear-gradient(
                to right,
                #ff9000 0,
                #ff5000 100%
            );
            border: none;
            border-radius: 60px;
            font-size: 18px;
            color: white;
            margin: 2px;
        }
    }
}
.wrapper {
    // height: 10vh;
    // overflow:auto;
    background-color: #f6f6f6;
    padding-top: 1px;
    .loader {
