// 轮播图
.ant-carousel .slick-slide {
    text-align: center;
    height: 200px;
    // line-height: 200px;
    background: gray;
    overflow: hidden;
    .carousel-img {
        width: auto;
        height: 200px;
        max-width: 100%;
        max-height: 100%;
    }
}

.ant-carousel .slick-slide h3 {
    color: #fff;
}
.vnavbar-container {
    width: 100%;
    display: flex;
    justify-content: center;
    padding: 5vw;
    box-sizing: border-box;
    margin: 0;
    .vnavbar-list {
        flex: 1;
        list-style: none;
        text-align: center;
        a {
            display: block;
            text-align: center;
            text-decoration: none;
            font-size: 22px;
            letter-spacing: 10px;
            padding-left: 10px;
            box-sizing: border-box;
            color: #888;
            width: 50%;
            margin: 0 auto;
        }
        .active {
            font-size: 24px;
            border-bottom: 6px solid #c8e8fc;
            color: black;
            font-weight: bold;
            /* transition: all 0.3s; */
        }
    }
}
.fade-enter {
    opacity: 0;
    z-index: 1;
}

.fade-enter.fade-enter-active {
    opacity: 1;
    transition: opacity 250ms ease-in;
}
.ant-modal-close{
    top: -50px;
    color: white;
}