.frap {
    position: fixed;
    bottom: 9vh;
    right: 1vw;
    z-index: 996;
    color: white;
    button {
        height: 7vh;
        //padding: 4vw;
        font-size: 1.3em;
        background-color: #71ceff;
        color: #FFF;
        border: 1px solid #71ceff;
        border-radius: 48px;
        font-weight: 700;
        box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.38);
        animation: pop 0.1s ease-out;
        transition-property: background, border-color, color, transform;
    }
    h3{
        color: white;
    }
}
.frap-in-modal{
    .frap-cell{
        padding: 2vw 0;
        margin: 2vw;
        border-radius: 15px;
        h3,a,p{
            color: white;
        }
    }

    .light-blue {
        background-color: lightblue;


    }

    .light-red {
        background-color: lightcoral;


    }

    .light-yellow {
        background-color: lightsalmon;

    }

    .light-green {
        background-color: lightgreen;
    }
}
