.my-favs{
    .fav-cell{
        list-style: none;
        border-radius: 10px;
        margin: 2vw;
        padding: 5vw 5vw;
        background-color: white;
        box-shadow: 0 5px 10px 0 rgba(174, 174, 174, 0.4);

        img{
            width: 100%;
        }
        span{
            font-size: 20px;
            font-weight: bold;
            padding: 3px 10px;
            border-radius: 50px;
            color: rgba(201, 160, 47, 0.9);
            background-color: rgba(255, 231, 166, 0.9);
        }
    }
}