
.product {
    border-radius: 10px;
    margin: 2vw;
    padding: 5vw 5vw;
    background-color: white;
    box-shadow: 0 5px 10px 0 rgba(174, 174, 174, 0.4);
    .img-product {
        img {
            width: 100%;
        }
    }
    img {
        width: 100%;
    }
    h2 {
        margin: 0;
    }
    .infs {
        margin: 5px;
        color: gray;
    }
    .price-and-pop {
        margin: 1px;
        .price {
            font-size: 20px;
            font-weight: bold;
            // display: inline-block;
            padding: 3px 10px;
            border-radius: 50px;
            color: rgba(201, 160, 47, 0.9);
            background-color: rgba(255, 231, 166, 0.9);
        }
        .pop {
            margin-left: 10px;
            // display: inline-block;
            width: 40px;
            height: 18px;
            font-size: 14px;
            line-height: 18px;
            letter-spacing: 1px;
            // text-align: center;
            border-radius: 15px;
            color: #999;
            border: 1px solid #999;
        }
    }
    .button-copy {
        height: 30px;
        background-image: linear-gradient(
            to right,
            #ff9000 0,
            #ff5000 100%
        );
        border: none;
        border-radius: 60px;
        font-size: 18px;
        color: white;
        margin: 2px;
        padding: 0px 10px;
    }
    .button-share {
        height: 30px;
        background-image: linear-gradient(
            to right,
            #ff9000 0,
            #ff5000 100%
        );
        border: none;
        border-radius: 60px;
        font-size: 18px;
        color: white;
        margin: 2px;
    }
}

.share-shit{
    .words{
        display: inline-block;
        margin-left: 5px;
    }
}